/**
 * TravelMain.js
 * Main Landing View for travel
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { Col } from 'reactstrap';
import PropTypes from 'prop-types';

import PiggyBank from 'assets/images/FormLanding/InformationLogos/PiggyBank.svg';
import HandWithMoney from 'assets/images/FormLanding/InformationLogos/HandWithMoney.svg';
import CircuitBoard from 'assets/images/FormLanding/InformationLogos/CircuitBoard.svg';
import Clock from 'assets/images/FormLanding/InformationLogos/Clock.svg';
import Luggague from 'assets/images/FormLanding/InformationLogos/Luggauge.svg';
import Plane from 'assets/images/FormLanding/InformationLogos/Plane.svg';
import Medic from 'assets/images/FormLanding/InformationLogos/Medic.svg';
import Disaster from 'assets/images/FormLanding/InformationLogos/Disaster.svg';
import splashImg from 'assets/images/Travel/TravelSplash.svg';

import FormLandingNew from 'components/Templates/FormLanding/FormLandingNew';
import TokioMarinePopUp from 'components/TokioMarinePopUp/TokioMarinePopUp';
import FormLandingInformation from 'components/Templates/FormLanding/FormLandingInformation';
import FieldGenerator from 'components/FormsV3/FieldGenerator';

import TravelEditDetail from './TravelEditDetail';

import './Travel.scss';

class TravelMain extends Component {
  constructor(props) {
    super(props);

    this.state = {
      showTMModal: false,
    };

    this.openTMModal = this.openTMModal.bind(this);
    this.closeTMModal = this.closeTMModal.bind(this);
  }

  openTMModal() {
    this.setState({ showTMModal: true });
  }

  closeTMModal() {
    this.setState({ showTMModal: false });
  }

  render() {
    const { showTMModal } = this.state;
    const {
      travelPromoGiveAway,
      onChangeValue,
      values,
      errors,
      mainFormFields,
      validateForm,
      onSubmit,
      handleGoNext,
    } = this.props;

    return (
      <div className="travel-container ">
        <FormLandingNew
          splashImg={splashImg}
          imgAlt="PolicyPal Travel Insurance"
          promo={travelPromoGiveAway}
          desc={
            <Fragment>
              <h1>Get 5% Cashback </h1>
              <h1>upon purchase of Travel Insurance!</h1>
            </Fragment>
          }
          formInformation={(
            <Fragment>
              <Col xs={12}>
                <FormLandingInformation
                  colorStyle="landing-green-style"
                  title="BENEFITS"
                  subTitle="Why you need Travel Insurance?"
                  data={[
                    {
                      img: Luggague,
                      title: 'Protect Your Belongings',
                      desc: 'Your personal belongings are as important to us as they are to you. Have a peace of mind in the event you lose or damage your belongings.',
                    }, {
                      img: Plane,
                      title: 'Flight Delay or Cancellation',
                      desc: 'Your flights may be delayed or cancelled due to unforeseen circumstances like bad weather. Protect yourself from lost time.',
                    }, {
                      img: Medic,
                      title: 'Medical Coverage',
                      desc: 'Your healthcare is looked out for no matter which part of the world you travel to. Worry about one less thing when you seek medical attention.',
                    }, {
                      img: Disaster,
                      title: 'Disasters Protection',
                      desc: 'Your risk exposure to natural disasters can be covered with insurance - get protected against earthquakes, hurricanes, tsunami & more.',
                    },
                  ]}
                />
              </Col>
              <Col className="travel-top-info" xs={12}>
                <FormLandingInformation
                  colorStyle="landing-blue-style"
                  title="HOW IT WORKS"
                  subTitle="It's better to buy from PolicyPal"
                  data={[
                    {
                      img: PiggyBank,
                      title: 'Value for Money',
                      desc: 'We compare policies listed by our partners to give you a comprehensive overview of the coverages best suited to your needs at the most affordable price.',
                    }, {
                      img: HandWithMoney,
                      title: 'Cashback Benefits',
                      desc: 'We reward you with 5% cashback in P$ credits with each Travel, Motor or Pet insurance purchase. Accumulate P$ credits & offset your subsequent purchases.',
                    }, {
                      img: CircuitBoard,
                      title: 'Automated Claims',
                      desc: 'We automatically process your claims from ERGO Automated Flight Delay Plans instantly and securely on our blockchain protocol.',
                    }, {
                      img: Clock,
                      title: 'Time-Saving',
                      desc: 'We store your details to save you time and replicate the information on your subsequent purchases on our one-stop platform.',
                    },
                  ]}
                />
              </Col>
            </Fragment>
          )}
        >
          {
            mainFormFields &&
            <TravelEditDetail
              title="Edit Travel Insurance Details"
              fields={mainFormFields}
              values={values}
              validateForm={validateForm}
              onSubmit={() => onSubmit(this.openTMModal)}
            >
              {
                _.map(mainFormFields, (f, index) => (
                  <FieldGenerator
                    key={`business-form-${index}`}
                    values={values}
                    errors={errors}
                    fieldData={f}
                    onChangeValue={(type, name, value) => onChangeValue(type, name, value, mainFormFields)}
                    extraInfo={f.extra_info && <p>{f.extra_info}</p>}
                    noInternalDesc={f.no_internal_desc}
                  />
                ))
              }
            </TravelEditDetail>
          }
        </FormLandingNew>
        <TokioMarinePopUp
          showModal={showTMModal}
          closeModal={() => {
            this.closeTMModal();
            handleGoNext(false);
          }}
          onSkip={handleGoNext}
          onInterested={() => handleGoNext(true)}
        />
      </div>
    );
  }
}

TravelMain.defaultProps = {
  travelPromoGiveAway: null,
  mainFormFields: null,
  values: null,
  errors: null,
};

TravelMain.propTypes = {
  mainFormFields: PropTypes.array,
  travelPromoGiveAway: PropTypes.object,
  onChangeValue: PropTypes.func.isRequired,
  values: PropTypes.object,
  errors: PropTypes.object,
  onSubmit: PropTypes.func.isRequired,
  validateForm: PropTypes.func.isRequired,
  handleGoNext: PropTypes.func.isRequired,
};

export default TravelMain;
