/**
 * ChangeInitialFormData.js
 * This is the green pencil button that appears mainly on
 * the compare view which will render the Edit Detail forms
 * of each category.
 *
 * Users will be able to change that form through this component
 * and will do a callback to update any changes, eg for compare view.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import {
  Row,
  Col,
} from 'reactstrap';
import PropTypes from 'prop-types';

import PopupBanner from 'assets/images/TokioMarine/PopupBanner.svg';
import PopupBannerResp from 'assets/images/TokioMarine/PopupBannerResp.svg';
import Plane from 'assets/images/TokioMarine/Plane.svg';
import Hospital from 'assets/images/TokioMarine/Hospital.svg';
import Language from 'assets/images/TokioMarine/Language.svg';

import ModalPopup from 'components/Templates/ModalPopup/ModalPopup';
import Button from 'components/FormsV3/Button/Button';

import './TokioMarinePopUp.scss';

const infoData = [{
  img: Hospital,
  title: 'Cashless service over 300 medical facilities',
  desc: 'Tokio Marine refers you to the nearest hospital most suited to your urgent needs and pays medical expenses directly to the medical facility.',
}, {
  img: Plane,
  title: 'Emergency repatriation',
  desc: 'Tokio Marine offers full support of repatriation to a medical institution in Singapore.',
}, {
  img: Language,
  title: 'Translation',
  desc: 'Tokio Marine provides medical care with interpreting service via phone in both English and Mandarin.',
}];

class TokioMarinePopUp extends Component {
  renderBanner() {
    return (
      <Row noGutters>
        <Col xs={12} className="banner">
          <img className="d-none d-sm-block" src={PopupBanner} alt="PopupBanner.svg" />
          <img className="d-block d-sm-none" src={PopupBannerResp} alt="PopupBannerResp.svg" />
        </Col>
      </Row>
    );
  }

  renderInfo() {
    return (
      <Row>
        {
          _.map(infoData, (data, index) => (
            <Col
              key={`tm-info-${index}`}
              xs={12}
              lg={4}
              className="info-container"
            >
              <div className="image">
                <img src={data.img} alt={`tm-info-${index}.svg`} />
              </div>
              <div className="desc">
                <h4>{data.title}</h4>
                <p>{data.desc}</p>
              </div>
            </Col>
          ))
        }
      </Row>
    );
  }

  renderButtons() {
    const { onSkip, onInterested, closeModal } = this.props;

    return (
      <Row className="d-flex justify-content-center" noGutters>
        <Col className="button-container">
          <Row className="w-100" noGutters>
            <Col className="button-container-inner" xs={{ size: 12, order: 2 }} sm={{ size: 6, order: 1 }}>
              <Button
                className="button-left"
                variant="medium-outline"
                height={48}
                onClick={() => { closeModal(); onSkip(); }}
              >
                <p>Skip</p>
              </Button>
            </Col>
            <Col className="button-container-inner" xs={{ size: 12, order: 1 }} sm={{ size: 6, order: 2 }}>
              <Button
                type="submit"
                variant="medium-flat-green"
                height={48}
                onClick={() => { closeModal(); onInterested(); }}
              >
                <p>Iâ€™m interested!</p>
              </Button>
            </Col>
          </Row>
        </Col>
      </Row>
    );
  }

  render() {
    const { showModal, closeModal } = this.props;

    return (
      <ModalPopup
        showModal={showModal}
        closeModal={closeModal}
        className="tokio-marine-xplora-japan-popup"
        backdropClassName="blue-modal-backdrop"
        header={<span>A Japan-Exclusive Plan Not to Be Missed!</span>}
        body={(
          <Fragment>
            {this.renderBanner()}
            {this.renderInfo()}
            {this.renderButtons()}
          </Fragment>
        )}
      />
    );
  }
}

TokioMarinePopUp.defaultProps = {
  showModal: false,
  onSkip: () => {},
  onInterested: () => {},
};

TokioMarinePopUp.propTypes = {
  showModal: PropTypes.bool,
  closeModal: PropTypes.func.isRequired,
  onSkip: PropTypes.func,
  onInterested: PropTypes.func,
};

export default TokioMarinePopUp;
