/**
 * TravelEditDetail.js
 * Edit Detail View for travel
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { Row, Col, Form } from 'reactstrap';
import PropTypes from 'prop-types';

import Button from 'components/FormsV3/Button/Button';


class TravelEditDetail extends Component {
  constructor(props) {
    super(props);

    const { values, setCloseCallBack } = this.props;
    this.originalValues = _.cloneDeep(values);

    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleCloseEdit = this.handleCloseEdit.bind(this);
    this.handleRevert = this.handleRevert.bind(this);

    if (setCloseCallBack) {
      setCloseCallBack(this.handleRevert);
    }
  }

  async handleSubmit(e) {
    e.preventDefault();

    const {
      closePopUp,
      fields,
      validateForm,
      onSubmit,
    } = this.props;

    await validateForm(() => {
      if (closePopUp) {
        closePopUp();
      }

      onSubmit();
    }, fields);
  }

  handleRevert() {
    const { onUpdateValues } = this.props;

    if (onUpdateValues) {
      onUpdateValues(this.originalValues);
    }
  }

  handleCloseEdit() {
    const { closePopUp } = this.props;
    closePopUp(this.handleRevert);
  }

  render() {
    const {
      isPopUp,
      title,
    } = this.props;

    const borderStyle = isPopUp ? '' : 'landing-form-container-border';

    return (
      <div className={`landing-form-container-new ${borderStyle}`}>
        {
          isPopUp ?
            <p className="edit-header">{title}</p>
          :
            null
        }
        <Form onSubmit={this.handleSubmit}>
          <Row className="input-offset" noGutters>
            {this.props.children}
            {
              !isPopUp ?
                <Col className="d-flex align-items-end justify-content-end button" xs={12} sm={6}>
                  <Button type="submit" variant="medium-flat-green" height={48}>
                    <p>Search Policies</p>
                  </Button>
                </Col>
              :
                null
            }
          </Row>
          <Row className="align-items-center button-container" noGutters>
            {
              isPopUp ?
                <Col className="justify-content-center button" xs={12}>
                  <Row noGutters>
                    <Col className="button-container-inner" xs={12} sm={6}>
                      <Button
                        type="button"
                        className="button-left"
                        variant="medium-outline-inverse"
                        height={48}
                        onClick={() => this.handleCloseEdit()}
                      >
                        <p>Cancel</p>
                      </Button>
                    </Col>
                    <Col className="button-container-inner" xs={12} sm={6}>
                      <Button
                        type="submit"
                        variant="medium-flat-green"
                        height={48}
                      >
                        <p>Save</p>
                      </Button>
                    </Col>
                  </Row>
                </Col>
              :
                null
            }
          </Row>
        </Form>
      </div>
    );
  }
}

TravelEditDetail.defaultProps = {
  values: null,
  closePopUp: null,
  setCloseCallBack: null,
  isPopUp: false,
  onUpdateValues: null,
};

TravelEditDetail.propTypes = {
  values: PropTypes.object,
  isPopUp: PropTypes.bool,

  closePopUp: PropTypes.func,
  setCloseCallBack: PropTypes.func,

  onUpdateValues: PropTypes.func,
  title: PropTypes.string.isRequired,

  onSubmit: PropTypes.func.isRequired,
};

export default TravelEditDetail;
