/**
 * FormLanding.js
 * Wrapper that takes in the main landing form and
 * renders the 3 images with description, along
 * with the promo button
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import React, { Component, Fragment } from 'react';
import { withRouter } from 'react-router-dom';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import Mouse from 'assets/images/FormLanding/Mouse.svg';

import FormLandingSplash from './FormLandingSplash';
import FormLandingFooter from './FormLandingFooter';
import PromoPopup from '../PromoPopup/PromoPopup';

import {
  onPageInsurance,
  setStateInsurance,
  onPageMore,
  setStateMore,
  onPageBusiness,
  setStateBusiness,
} from '../../../actions/navActions';

import './FormLandingNew.scss';

class FormLandingNew extends Component {
  constructor(props) {
    super(props);

    // Set Insurance Page Indicator
    const pageState = onPageInsurance.ACTIVE;
    // Reset More Page Indicator
    const resetMore = onPageMore.NONE;

    // Reset Business Page Indicator
    const resetBusiness = onPageBusiness.NONE;

    const {
      setPage,
      resetIndicatorMore,
      resetIndicatorBusiness,
    } = this.props;
    setPage(pageState);
    resetIndicatorMore(resetMore);
    resetIndicatorBusiness(resetBusiness);

    this.renderBackGround = this.renderBackGround.bind(this);
  }

  renderBackGround() {
    return (
      <Fragment>
        <div className="circle-shape circle-upper" />
        <div className="circle-shape circle-bottom" />
        <div className="circle-green" />
      </Fragment>
    );
  }

  render() {
    const {
      splashImg,
      desc,
      formInformation,
    } = this.props;

    return (
      <Fragment>
        <Row noGutters>
          <Col className="landing-form-outer-container-new">
            {this.renderBackGround()}
            <Row noGutters>
              <Col xs={12}>
                <div className="d-block d-lg-none">{desc}</div>
              </Col>
              <Col xs={12} sm={7} className="d-none d-lg-block">
                <FormLandingSplash
                  desc={desc}
                  img={splashImg}
                />
              </Col>
              <Col xs={12} lg={5}>
                {this.props.children}
              </Col>
              <Col xs={12} className="mouse d-none d-md-flex">
                <img src={Mouse} alt="Mouse.svg" />
              </Col>
              {formInformation}
            </Row>
          </Col>
        </Row>
        <FormLandingFooter />
        <PromoPopup
          ref={node => this.promoPopup = node}
        />
      </Fragment>
    );
  }
}

FormLandingNew.defaultProps = {
  setPage: null,
  resetIndicatorMore: null,
  resetIndicatorBusiness: null,
  formInformation: null,
};

FormLandingNew.propTypes = {
  splashImg: PropTypes.string.isRequired,
  desc: PropTypes.object.isRequired,
  setPage: PropTypes.func,
  resetIndicatorMore: PropTypes.func,
  resetIndicatorBusiness: PropTypes.func,
  formInformation: PropTypes.object,
};

const mapDispatchToProps = dispatch => ({
  setPage: (pageState) => {
    dispatch(setStateInsurance(pageState));
  },
  resetIndicatorMore: (resetMore) => {
    dispatch(setStateMore(resetMore));
  },
  resetIndicatorBusiness: (resetBusiness) => {
    dispatch(setStateBusiness(resetBusiness));
  },
});

export default withRouter(connect(null, mapDispatchToProps)(FormLandingNew));
