/**
 * TravelMainContainer.js
 * The main container for travel
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import window from 'global/window';
import _ from 'lodash';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';

import TravelMain from 'components/Travel/TravelMain';

import { setCategoryNavBar } from 'actions/navActions';
import { setInsuranceFormState, FORMSTATES } from 'actions/insuranceActions';
import { getMainFormFields } from 'actions/travelActions';

import { daysDiff } from 'lib/dateFunctions';
import { fbTag, gaEvent, ampTag, gaTag } from 'lib/tagFunctions';

class TravelMainContainer extends Component {
  constructor(props) {
    super(props);

    this.onSubmit = this.onSubmit.bind(this);
    this.handleGoNext = this.handleGoNext.bind(this);
  }
  async componentDidMount() {
    const {
      setFormState,
      doSetCategoryNavBar,
      history,
      handleAPI,
      mainFormFields,
      getMainFields,
      constants,
      setPlansData,
    } = this.props;

    setFormState(FORMSTATES.MAIN.index);

    window.scrollTo(0, 0);
    fbTag('PPS2 - Pageview - TravelPage', { Product: 'Travel' });
    gaTag({
      page_title: 'Travel',
      page_path: `/travel${history.location.search}`,
    });
    ampTag('TravelFormS');
    doSetCategoryNavBar(true);

    if (!mainFormFields) {
      const mainFields = await handleAPI(
        () => getMainFields(constants),
        false,
        'Unable to get form fields. Please try again!',
        () => history.goBack(),
      );

      if (!mainFields) {
        history.push('/');
      }
    }

    setPlansData();
  }

  shouldComponentUpdate(nextProps) {
    const { mainFormFields, values, errors } = this.props;

    return !_.isEqual(mainFormFields, nextProps.mainFormFields) ||
          !_.isEqual(values, nextProps.values) ||
          !_.isEqual(errors, nextProps.errors);
  }

  componentWillUnmount() {
    const { doSetCategoryNavBar } = this.props;
    doSetCategoryNavBar(false);
  }

  async onSubmit(openTMModal) {
    const { values } = this.props;

    fbTag('PPS2 - ButtonClick - Travel - GetQuote', { Product: 'Travel', Click_Text: 'GetQuote' });
    gaEvent('Travel - Get Quote', {
      event_category: 'Travel',
      event_label: 'Button - Travel - Get Quote',
    });

    const adults = (values.num_adult && values.num_adult.value) || 0;
    const children = (values.num_children && values.num_children.value) || 0;
    const travellers = adults + children;

    const tripType = values.trip_type && values.trip_type.value;
    const planType = values.plan_type && values.plan_type.value;

    const countries = values.countries && values.countries.value;
    const region = values.region && values.region.value;

    ampTag('TravelGetQuoteA', {
      type: tripType,
      for: planType,
      destination: tripType === 'Single Trip' ? countries : region,
      duration: tripType === 'Annual Multi-Trip' ? 365 : daysDiff(values.start_date && values.start_date.value, values.end_date && values.end_date.value),
      adults,
      children,
      travellers,
    });

    // TM Explora Japan
    const isJapan = countries && countries.length === 1 && _.find(countries, country => _.lowerCase(country.value) === 'japan');
    if (isJapan) {
      openTMModal();
      return;
    }

    this.handleGoNext();
  }

  handleGoNext(goToProduct) {
    const { setFormState, history } = this.props;
    setFormState(FORMSTATES.CHOOSEPLAN.index);
    const productLink = goToProduct ? '/tokio_marine/xplora_japan' : '';
    history.push(`/travel${FORMSTATES.CHOOSEPLAN.path}${productLink}`);
  }

  render() {
    return (
      <TravelMain
        {...this.props}
        onSubmit={this.onSubmit}
        handleGoNext={this.handleGoNext}
      />
    );
  }
}

TravelMainContainer.defaultProps = {
  mainFormFields: null,
  travelPromoGiveAway: null,
  values: null,
  errors: null,
};

TravelMainContainer.propTypes = {
  values: PropTypes.object,
  errors: PropTypes.object,
  onChangeValue: PropTypes.func.isRequired,
  getMainFields: PropTypes.func.isRequired,
  mainFormFields: PropTypes.array,
  travelPromoGiveAway: PropTypes.object,
  history: PropTypes.object.isRequired,
  setFormState: PropTypes.func.isRequired,
  doSetCategoryNavBar: PropTypes.func.isRequired,
  validateForm: PropTypes.func.isRequired,
  setPlansData: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  mainFormFields: state.travel.mainFormFields,
  travelPromoGiveAway: state.insurance.promoGiveaways.travel,
  constants: state.insurance.constants,
});

const mapDispatchToProps = dispatch => ({
  getMainFields: constants => dispatch(getMainFormFields(constants)),
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
  doSetCategoryNavBar: (isCategory) => {
    dispatch(setCategoryNavBar(isCategory, true));
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(TravelMainContainer));
