/**
 * FormLanding.js
 * Wrapper that takes in the main landing form and
 * renders the 3 images with description, along
 * with the promo button
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

import './FormLandingInformation.scss';

class FormLandingInformation extends Component {
  renderData() {
    const { data, title } = this.props;
    const width = data && data.length > 0 ? `${(100.0 / data.length)}%` : '0%';

    return _.map(data, d => (
      <div
        key={`${title}-${d.title}`}
        className="desc-container"
        style={{ width }}
      >
        <div className="img-container">
          <img src={d.img} alt={d.title} />
        </div>
        <h3>{d.title}</h3>
        <h5>{d.desc}</h5>
      </div>
    ));
  }

  render() {
    const {
      title,
      subTitle,
      colorStyle,
    } = this.props;

    return (
      <Row className={`form-landing-information-container ${colorStyle}`} noGutters>
        <Col xs={12}>
          <h2 className="main-title">{title}</h2>
          <h2 className="sub-title">{subTitle}</h2>
        </Col>
        <Col xs={12} className="information-container">
          {this.renderData()}
        </Col>
      </Row>
    );
  }
}

FormLandingInformation.defaultProps = {
};

FormLandingInformation.propTypes = {
  title: PropTypes.string.isRequired,
  subTitle: PropTypes.string.isRequired,
  colorStyle: PropTypes.string.isRequired,
  data: PropTypes.array.isRequired,
};

export default FormLandingInformation;
